<template>
    <main>
        <span class="BItitle">我的收藏</span>
        <div v-for="item in collectList" class="borrow-item">
            <div class="borrow-info">
                <div class="borrow-book-info">
                    <span class="borrow-book-code">{{item.bookinfo.Code}}</span>
                    <span class="borrow-book-name">{{item.bookinfo.name}}</span>
                </div>
            </div>
            <button class="return-book-btn" @click="cancelCollect(item.bookId)">取消收藏</button>
            <div class="clr"></div>
        </div>
        <button class="return-btn" @click="changed()">返回</button>
    </main>
</template>
<style scoped>
    main {
        max-width:1150px;
        margin: 0 auto;
        padding: 0 50px;
    }
    .BItitle {
        display: block;
        width: 100%;
        border-bottom: 1px solid #bbb;
        font-size: 36px;
        font-weight: 500;
        line-height: 1.1;
        margin: 20px 0 10px 0;
        text-align: left;
        padding-top: 20px;  
        padding-bottom: 10px;     
    }
    .borrow-info {
        float: left;
        width: 80%;
        overflow: hidden;
    }
    .borrow-book-info {
        padding: 10px 10px;
        text-align: left;
        white-space: nowrap;
    }
    .borrow-book-code {
        font-weight: 600;
        font-size: 1.5rem;
    }
    .borrow-book-name {
        font-size: 1.5rem
    }
    .return-book-btn {
        float: right;
    }
    .return-book-btn,
    .return-btn {
        margin-top: 20px;
        height: 35px;
        padding: 3px 10px;
        /*width: 60px;*/
        border-radius: 5px;
        background-color: #337ab7;
        border: 1px solid #337ab7;
        cursor: pointer;
        color: #fff;
        outline: none;    
    }
    .return-book-btn:hover,
    .return-btn:hover {
        background-color: #2a5885;
    }
    .clr {
        clear: both;
    }
</style>
<script>
    import qs from 'qs'
    export default{
        data(){
            return {
                collectList:[],
            }
        },
        created:function(){
            this.getUserCollectInfo();
        },
        methods:{
            getUserCollectInfo(){
                this.$axios.post('/user/getUserInfo')
                .then((res)=>{
                    this.collectList = res.data.collectInfo;
                })
            },
            cancelCollect(book_id){
                let data = {'book_id':book_id};
                this.$axios.post(
                    '/collect/no_collect',
                    qs.stringify(data),
                    {
                        headers:{
                            'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                            'X-Requested-With':'XMLHttpRequest',
                        }
                    }
                )
                .then((res)=>{
                    if (res.data.result=="true") {
                        this.getUserCollectInfo();
                        this.$emit("flesh");
                    }
                })
                .catch((res)=>{
                    this.$router.push({name:'Login'});
                })
            },
            changed(){
                this.$router.push({name:'BookShelf'});
            }
        }
    }
</script>



