<template>
    <div class="desktop">
        <header>
            <div class="header-content">
                <div class="header-left">
                    <div class="userGreet">
                        <span>{{username}}&nbsp;</span>你好~
                        <p>{{department}}</p>
                    </div>
                    <div class="Borrowing" @click="borrowInfo()">
                        借阅<span>{{borrowNum}}</span>
                    </div>
                    <div class="collection" @click="collectInfo()">
                        收藏<span>{{collectNum}}</span>
                    </div>
                    <div class="modifyPassward" @click="changePassword()">密码修改</div>
                </div> 
                <div class="header-right">
                    <button class="signOut" @click="logout()">注销</button>
                </div>                
            </div>
        </header>
        <router-view @flesh="getUserInfo()"></router-view>
    </div>
</template>
<style scoped>
    header {
        border-bottom: 1px solid #ccc;
    }
    .header-content {
        display: flex;
        justify-content: space-between;
        height: 60px;
        max-width: 1150px;
        margin: 0 auto;
        padding: 10px 50px;
        line-height: 60px;
        
    }
    .header-left {
        display: flex;
        flex-basis: 50%;
        justify-content: flex-start;
        white-space: nowrap;
    }
    .userGreet {
        font-size: 28px;
    }
    .userGreet span {
        font-weight: 600;
    }
    .userGreet p {
        margin: -10px 0 0 0;
        font-size: 1rem;
        text-align: left;
        line-height: 1rem;
    }
    .Borrowing,
    .collection,
    .modifyPassward {
        /*color: #337ab7;*/
        height: 40px;
        line-height: 20px;
        margin: 10px 0;
        padding: 0 10px;
        letter-spacing: .1em;
        cursor: pointer;
    }
    .modifyPassward {
        line-height: 40px;
    }
    .Borrowing:hover,
    .collection:hover,
    .modifyPassward:hover {
        background-color: #ddd;
        border-radius: 5px;
    }
    .Borrowing span,
    .collection span{
        display: inline-block;
        width: 28px;
        height: 20px;
        background-color: #777;
        margin: 10px 0;
        color: #fff;
        border-radius: 10px;
        text-align: center;
    }   
    .header-right {
        flex-basis: 50%;
        text-align: right;
    }
    .signOut {
        height: 35px;
        width: 60px;
        border-radius: 5px;
        background-color: #337ab7;
        border: 1px solid #337ab7;
        cursor: pointer;
        color: #fff;
        outline: none;
    }
    .signOut:hover {
        background-color: #2a5885;
    }
</style>

<script>
    import qs from 'qs'
    export default {
        data(){
            return {
                username:"",
                passward:"",
                userId:"",
                department:"",
                user:"",
                //用户借书数量
                borrowNum:0,
                //用户收藏数量
                collectNum:0,
            }
        },
        created:function(){
            this.getUserInfo();
        },
        methods:{
            logout(){
                this.$axios.post('/user/logout').then((res)=>{
                    if (res.data.result=="true") {
                        this.$router.push({name:'Login'});
                    }
                });
            },
            getUserInfo(){
                this.$axios.post('/user/getUserInfo')
                .then((res)=>{
                    this.username = res.data.userInfo.username;
                    this.department = res.data.userInfo.department;
                    this.userId = res.data.userInfo.id;
                    if (res.data.borrowInfo=="") {
                        this.borrowNum = 0;
                    }else{
                        this.borrowNum = res.data.borrowInfo.length; 
                    }
                    if (res.data.collectInfo=="") {
                        this.collectNum = 0;
                    }else{
                        this.collectNum = res.data.collectInfo.length; 
                    }
                }).catch((error)=>{this.$router.push({name:'Login'});})
            },
            changePassword(){
                this.$router.push({name:'changePassword'});
            },
            borrowInfo(){
                this.$router.push({name:'BorrowInfo'});
            },
            collectInfo(){
                this.$router.push({name:'CollectInfo'});
            }
        },
        
    }
</script>


