import Vue from 'vue'
import VueRouter from 'vue-router'
/*引入页面*/
import Login from './components/login/login.vue'
import Desktop from './components/desktop/desktop.vue'
import BookShelf from './components/desktop/bookShelf.vue'
import ChangePassWord from './components/desktop/changePassword.vue'
import BorrowInfo from './components/desktop/borrowInfo.vue'
import CollectInfo from './components/desktop/collectInfo.vue'
import BookDetail from './components/desktop/bookDetail.vue'
import Start from './components/desktop/start.vue'
import Management from './components/management/management.vue'
// import ManageBook from './components/management/manageBook.vue'

Vue.use(VueRouter);

/*配置路由*/
const routes = [
{
    path: '/login',
    name: 'Login',
    component: Login
},
{
    path: '/desktop',
    component: Desktop,
    children:[
    {
        path:"/bookShelf",
        name:'BookShelf',
        component:BookShelf,
    },
    {
        path:"/changePassword",
        name:'changePassword',
        component:ChangePassWord
    },
    {
        path:"/borrowInfo",
        name:'BorrowInfo',
        component:BorrowInfo
    },
    {
        path:"/collectInfo",
        name:'CollectInfo',
        component:CollectInfo
    },
    {
        path:"/bookDetail",
        name:'BookDetail',
        component:BookDetail
    },
    {
        path:'',
        component:BookShelf
    }
    ]
},
{
    path:"/management",
    name:'management',
    component:Management,
},
{
    path:'/',
    component: Login
}
]

var router =  new VueRouter({
    routes,
    // mode:"history"
})
router.beforeEach((to,from,next)=>{
    if (to.meta.title) {
        document.title = to.meta.title
}
next()
})
export default router; 


