<template>
    <div class="mangement">
        <header>
            <button class="signOut" @click="logout()">注销</button>
            <h1 class="mange-title">后台管理系统页面</h1>
            
        </header>
        <nav>
            <div class="nav-item" @click="changeToBookPage()">管理书籍</div>
            <div class="nav-item" @click="changeToUserPage()">用户管理</div>
            <div class="nav-item" @click="changeToBorrowInfoPage()">借阅情况</div>
        </nav>
        <main>
            <div class="bookPage" v-if="manageType === 'bookPage'">
                <div class="getBookPage">
                    <div>
                        <button class="select-but" @click="active=!active"><span></span></button>
                        <div v-show="active" class="select-content">
                            <ul >
                                <li v-for="(item,index) in allCode" @click="changeCode(index,item.code)" :class="clickIndex == index ? 'selectd' : ''" >{{item.code}}</li>
                            </ul>
                        </div>
                        <span class="book-code">{{selectdCode}}</span>
                    </div>
                    <button class="com-btn" @click="addBook()">添加书籍</button>
                </div>
                <div class="book-table" v-if="pageType === 'bookTable'">
                    <div class="book-search">
                        <input type="text" class="book-search-input"  @keyup.enter="searchBook()" v-model="bookName">
                        <input type="button" class="book-search-but" value="搜索" @click="searchBook()">
                    </div>
                    <table class="B-table">
                        <thead>
                            <tr>
                                <th>编号</th>
                                <th>书名</th>
                                <th>创建时间</th>
                                <th>所在位置</th>
                                <th>操作</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="item in bookData">
                                <td>{{item.code}}</td>
                                <td>{{item.name}}</td>
                                <td>{{item.createtime}}</td>
                                <td>{{item.location}}</td>
                                <td>
                                    <button class="op-btn op-modify-btn" @click="updateBook(item.id)">修改</button>
                                    <button class="op-btn op-delete-btn" @click="deleteBook(item.id)">删除</button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <div class="table-footer">
                        <button class="com-btn" @click="prePage()">上一页</button>
                        <div>
                            <input type="text" class="turn-input" v-model="changePageIndex" @keyup.enter="changePage()">
                            <span>/{{totalPage}}</span>
                            <button class="com-btn turn-input-btn" @click="changePage()">跳转</button>
                        </div>
                        
                        <button class="com-btn" @click="nextPage()">下一页</button>
                    </div>
                </div>
                <div class="uploadBook" v-else-if="pageType === 'uploadBook'">
                    <div class="upload-img">
                        <div class="selectImg">
                            <div class="selectImgButton">
                                <label>
                                    <input type="file" name="upload" @change="getFile($event)"> 
                                    选择Img
                                </label>
                            </div>
                        </div>
                    </div>
                    <div class="img-detail">
                        <div class="img-detail-item">
                            <span>书名：</span>
                            <input type="text" v-model.lazy="uploadBookName">
                        </div>
                        <div class="img-detail-item">
                            <span>编号：</span>
                            <input type="text" v-model.lazy="uploadBookCode">
                        </div>
                        <div class="img-detail-item">
                            <span>位置：</span>
                            <input type="text" v-model.lazy="uploadBookLocation">
                        </div>
                        <div class="img-detail-item">
                            <span>类型：</span>
                            <input type="text" v-model.lazy="uploadBookType">
                        </div>
                        
                    </div>
                    <div>
                        <button @click="turn()" class="turn-btn">返回</button>
                        <button @click="upload()" class="upload-btn">上传</button>
                    </div>        
                </div>
            </div>
            <div class="userPage" v-else-if="manageType === 'userPage'">
                <button class="com-btn addUser-btn" @click="addUser()">添加用户</button>
                <div class="book-table" v-if="pageType === 'userTable'">
                    <table class="B-table">
                        <thead>
                            <tr>
                                <th>序号</th>
                                <th>账号</th>
                                <th>姓名</th>
                                <th>部门</th>
                                <th>操作</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item,index) in userData">
                                <td>{{(curpage-1)*pageSize+index+1}}</td>
                                <td>{{item.user}}</td>
                                <td>{{item.username}}</td>
                                <td>{{item.department}}</td>
                                <td>
                                    <button class="op-btn op-modify-btn" @click="updateUser(item.id)">修改</button>
                                    <button class="op-btn op-delete-btn" @click="deleteUser(item.id)">删除</button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <div class="table-footer">
                        <button class="com-btn" @click="prePage()">上一页</button>
                        <div>
                            <input type="text" class="turn-input" v-model="changePageIndex" @keyup.enter="changePage()">
                            <span>/{{totalPage}}</span>
                            <button class="com-btn turn-input-btn" @click="changePage()">跳转</button>
                        </div>
                        
                        <button class="com-btn" @click="nextPage()">下一页</button>
                    </div>
                </div>
                <div class="uploadUser" v-else-if="pageType === 'uploadUser'">
                    <div class="uploadUserForm">
                        <span class="uploadUserTips">姓名：</span>
                        <input class="uploadUserItem" type="text" v-model.lazy="uploadUserName">
                    </div>
                    <div class="uploadUserForm">
                        <span class="uploadUserTips">账号名：</span>
                        <input class="uploadUserItem" type="text" v-model.lazy="uploadUser">
                    </div>
                    <div class="uploadUserForm">
                        <span class="uploadUserTips">密码：</span>
                        <input class="uploadUserItem" type="password" v-model.lazy="uploadPassword">
                    </div>
                    <div class="uploadUserForm">
                        <span class="uploadUserTips">部门：</span>
                        <input class="uploadUserItem" type="text" v-model.lazy="uploadDepartment">
                    </div>
                    <div>
                        <button @click="turn()" class="turn-btn">返回</button>
                        <button @click="upload()" class="upload-btn">上传</button>
                    </div> 
                </div>
            </div>
            <div class="borrowInfoPage" v-else>
                <div class="book-table">
                    <table class="B-table">
                        <thead>
                            <tr>
                                <th>序号</th>
                                <th>书名</th>
                                <th>账号</th>
                                <th>借书时间</th>
                                <th>操作</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item,index) in borrowInfoData">
                                <td>{{(curpage-1)*pageSize+index+1}}</td>
                                <td>{{item.bookinfo.name}}</td>
                                <td>{{item.user.username}}</td>
                                <td>{{item.borrowtime}}</td>
                                <td>
                                    <button class="op-btn op-delete-btn" @click="deleteBorrowInfo(item.bookId,item.userId)">删除</button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <div class="table-footer">
                        <button class="com-btn" @click="prePage()">上一页</button>
                        <div>
                            <input type="text" class="turn-input" v-model="changePageIndex" @keyup.enter="changePage()">
                            <span>/{{totalPage}}</span>
                            <button class="com-btn turn-input-btn" @click="changePage()">跳转</button>
                        </div>
                        
                        <button class="com-btn" @click="nextPage()">下一页</button>
                    </div>
                </div>
            </div>
        </main>
    </div>
</template>
<style scoped>
    .mangement {
        max-width: 1150px;
        margin: 0 auto;
        position: relative;
    }
    .mangement header {
        margin-top: 1px;
        border: 1px solid #bbb;
        border-radius: 4px;
        /*box-shadow: inset 0 3px 5px rgba(0, 0, 0, .125);*/
    }
    .mange-title {
        padding: 2rem;
        margin: 0;
    }
    .signOut {
        position: absolute;
        right: 1rem;
        top: 2.3rem;
        height: 35px;
        width: 60px;
        border-radius: 5px;
        background-color: #337ab7;
        border: 1px solid #337ab7;
        cursor: pointer;
        color: #fff;
        outline: none;
    }
    .mangement nav {
        display: flex;
    }
    .nav-item {
        flex-basis: 33.3%;
        line-height: 3rem;
        font-size: 1.2rem;
        border: 1px solid #bbb;
        border-top: 0;
        /*background-color: #f4f4f4;*/
        color: #000;
        cursor: pointer;
    }
    .nav-item:first-child {
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
        border-right: 0;
    }
    .nav-item:last-child {
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
        border-left: 0;
    }
    .nav-item:hover {
        background-color: #ccc;
    }
    .nav-item-active {
        background-color: #3572ad;
        color: #fff;
    }
    .nav-item-active:hover {
        background-color: #2a5885;
    }

    .getBookPage {
        display: flex;
        justify-content: space-between;
        padding: 10px;
    }
    .select-but {
        display: inline-block;
        width: 34px;
        height: 34px;
        padding: 6px 12px;
        border: 1px solid #ccc;
        outline: 0;
        background-color: #fff;
        border: 1px solid #ccc;
        border-radius: 4px;
        cursor: pointer;
    }
    .select-but:hover {
        color: #333;
        background-color: #e6e6e6;
        border-color: #adadad;
        box-shadow: inset 0 3px 5px rgba(0, 0, 0, .125);
    }
    .select-but > span {
        display: block;
        border-top: 5px solid #333;
        border-right: 5px solid transparent;
        border-left: 5px solid transparent;
        border-bottom: 5px solid transparent;
        margin: 3px 0 0 0;
    }
    .book-code {
        display: inline-block;
        width: 44px;
        height: 20px;
        margin-left: -5px; 
        padding: 6px 12px;
        text-align: center;
        border: 1px solid #ccc;
        vertical-align: top;
        border-radius: 4px;
    }
    .select-content {
        position: absolute;
        width: 124px;
        border: 1px solid #ccc;
        border-radius: 4px;
        background-color: #fff;
        z-index: 999;
    }
    .select-content > ul {
        padding: 10px 0;
    }
    .select-content > ul > li{
        line-height: 1.7;
        cursor: pointer;
    }
    .select-content > ul > li:hover {
        background-color: #f4f4f4;
    }
    .selectd {
        background-color: #3572ad;
    }
    .com-btn {
        height: 35px;
        padding: 2px 10px;
        border-radius: 5px;
        background-color: #337ab7;
        border: 1px solid #337ab7;
        cursor: pointer;
        color: #fff;
        outline: none;
    }
    .book-table {
        border: 1px solid #ccc;
        border-radius: 4px;
        box-shadow: 1px 1px 1px #888888;
        padding: 10px;
    }
    .book-search {
        margin-bottom: 10px;
        display: flex;
    }
    .book-search-input {
        width: 90%;
        height: 28px;
        padding: 3px;
        border: 1px solid #ccc;
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
    }
    .book-search-but {
        width: 10%;
        /*margin-left: -10%; */
        border: 1px solid #ccc;
        border-left: 0; 
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
        background-color: #fff;
        cursor: pointer;
    }
    .B-table {
        width: 100%;
        font-family: verdana,arial,sans-serif;
        color:#333333;
        border-collapse: separate;
        border-spacing: 0;
    }
    .B-table th {
        border-top: 1px solid #999;
    }
    .B-table th,
    .B-table td {
        border-left: 1px solid #999;
        border-bottom: 1px solid #999;
        padding: 8px;
    }
    .B-table th:first-child {
        border-top-left-radius: 10px;
    }
    .B-table th:last-child {
        border-top-right-radius: 10px;
    }   
    .B-table tr td:last-child,
    .B-table tr th:last-child {
        border-right: 1px solid #999;
    }
    .B-table tr:last-child td:first-child {
        border-bottom-left-radius: 10px;
    }
    .B-table tr:last-child td:last-child {
        border-bottom-right-radius: 10px;
    }
    .op-btn {
        color: #fff;
        border: 0;
        padding: 3px 10px;
        border-radius: 4px;
        cursor: pointer;
    }
    .op-modify-btn {
        background-color: #58b157;
    }
    .op-modify-btn:hover {
        background-color: #429541;
    }
    .op-delete-btn {
        background-color: #eba34e;
    }
    .op-delete-btn:hover {
        background-color: #e68c2d;
    }
    .table-footer {
        padding: 20px;
        display: flex;
        justify-content: space-between;
        font-size: 1.2rem;
        line-height: 35px;
    }
    .turn-input {
        height: 25px;
        width: 35px;
        border: 1px solid #bbb;
        border-radius: 4px;
        padding: 0;
    }
    .turn-input-btn {
        height: 25px;
    }
    .uploadBook {
        border: 1px solid #ccc;
        border-radius: 4px;
        box-shadow: 1px 1px 1px #888888;
        padding: 50px 10px;
    }
    .upload-img {
        padding: 5px 16px;
        text-align: center;
        display: inline-block;
    }
    .selectImg {
        display: block;
        width: 300px;
        height: 190px;
        text-align: center;
        border: 3px dashed #bbb;
    }
    .selectImgButton {
        padding-top: 70px;
    }
    .selectImgButton label {
        display: inline-block;
        width: 100px;
        height: 50px;
        background: #27B1E9;
        border-radius: 4px;
        color: #FFF;
        line-height: 50px;
        font-size: 18px;
    }
    .selectImgButton label:hover {
        background: #229BCD;
    }
    .selectImgButton label input {
        width: 0;
        height: 0;
        opacity: .0;
    } 
    .img-detail {
        display: inline-block;
        padding: 5px 16px;
        vertical-align: middle;
    }
    .img-detail-item {
        display: block;
        margin: 10px;
    }
    .turn-btn,
    .upload-btn {
        /*display: block;*/
        margin: 20px auto;
        height: 35px;
        padding: 2px 20px;
        border-radius: 5px;
        background-color: #337ab7;
        border: 1px solid #337ab7;
        cursor: pointer;
        color: #fff;
        outline: none;
    }
    .userPage {
        margin-top: 10px;
    }
    .addUser-btn {
        position: relative;
        left: 45%;
        margin: 10px;
    }
    .uploadUserForm {
        padding: 10px;
        line-height: 1.5rem;
    }
    .uploadUserTips {
        display: inline-block;
        width: 40%;
        text-align: right;
        font-size: 1.5rem;
    }
    .uploadUserItem {
        display: inline-block;
        width: 20%;
        margin-right: 20%;
        text-align: left;
        padding: 0;
        border: 1px solid #bbb;
        line-height: 1.5rem;
        vertical-align: top;
    }
</style>
<script>
import qs from 'qs';
    export default{
        data(){
            return {
                manageType:'bookPage',
                active:false,
                allCode:[
                    {
                        code:'all',
                    },
                    {
                        code:'A1',
                    },
                    {
                        code:'A2',
                    },
                    {
                        code:'B1',
                    },
                    {
                        code:'B2',
                    },
                    {
                        code:'C1',
                    },
                    {
                        code:'C2',
                    },
                    {
                        code:'D1',
                    },
                    {
                        code:'D2',
                    },
                    {
                        code:'E1',
                    },
                    {
                        code:'E2',
                    },
                    {
                        code:'E3',
                    },
                    {
                        code:'F1',
                    },
                    {
                        code:'F2',
                    }
                ],
                selectdCode:'all',
                clickIndex:0,
                curpage:1,
                pageSize:10,
                totalPage:0,
                bookData:[],
                userData:[],
                borrowInfoData:[],
                changePageIndex:null,
                searchFlag:false,
                updateFlag:false,
                updateId:null,
                bookName:'',
                pageType:'bookTable',
                uploadImg:null,
                uploadBookName:null,
                uploadBookCode:null,
                uploadBookLocation:null,
                uploadBookType:null,
                uploadUser:null,
                uploadPassword:null,
                uploadDepartment:null,
                uploadUserName:null,
            }
        },
        created(){
            this.getPageData();
        },
        methods:{
            logout(){
                this.$axios.post('/user/logout').then((res)=>{
                    if (res.data.result=="true") {
                        this.$router.push({name:'Login'});
                    }
                });
            },
            changeCode(index,code){
                this.active = !this.active;
                this.selectdCode = code;
                this.clickIndex = index;
                this.curpage = 1;
                this.searchFlag = false;
                this.getPageData();     
            },
            getPageData(){
                if (this.searchFlag) {
                    this.getSreachBook();
                }else{
                    if (this.selectdCode=='all') {
                            var scode = '';
                        }else{
                            var scode = this.selectdCode;
                        }
                    let data = {
                        'currentPage':this.curpage,
                        'pageSize':this.pageSize,
                        'code':scode,
                        
                    }
                    this.$axios.post(
                        '/book/getBooksByPage',
                        qs.stringify(data),
                        {headers:{
                            'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                            'X-Requested-With':'XMLHttpRequest',
                        }}
                    )
                    .then((res)=>{
                        this.bookData = res.data.items;
                        this.totalPage = res.data.totalPage;
                        // console.log(this.bookData);
                        // console.log(res);
                    })
                    .catch((error)=>{
                        console.log(error);
                    })
                }
            },
            searchBook(){
                this.curpage = 1;
                this.searchFlag = true;
                this.getPageData();
            },
            getSreachBook(){
                let data = {
                    'keyWord' : this.bookName,
                    'currentPage' : this.curpage,
                    'pageSize' : this.pageSize
                }
                this.$axios.post(
                    '/book/searchBook',
                    qs.stringify(data),
                    {headers:{
                        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                        'X-Requested-With':'XMLHttpRequest',
                    }}
                )
                .then((res)=>{
                    this.bookData = res.data.items;
                    this.totalPage = res.data.totalPage;
                })
                .catch((error)=>{
                    console.log(error);
                })
            },
            addBook(){
                this.pageType = 'uploadBook';
                this.updateFlag = false;
            },
            updateBook(id){
                this.pageType = 'uploadBook';
                this.updateFlag = true;
                this.updateId = id;
            },
            deleteBook(id){
                let data = {'book_id':id};
                this.$axios.post(
                    '/management/deleteBook',
                    qs.stringify(data),
                    {headers:{
                        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                        'X-Requested-With':'XMLHttpRequest',
                    }}
                )
                .then((res)=>{
                    // console.log(res);
                    alert("删除成功！")
                    this.getPageData();
                })
                .catch((error)=>{
                    console.log(error);
                })
            },
            prePage(){
                
                if (this.curpage>1) {
                    this.curpage--;
                    if (this.manageType === 'bookPage') {
                        this.getPageData();
                    }else if (this.manageType === 'userPage') {
                        this.getUserPageData();
                    }else{
                        this.getBorrowInfoPageData();
                    }
                }
            },
            nextPage(){
                if (this.curpage<this.totalPage) {
                    this.curpage++;
                    if (this.manageType === 'bookPage') {
                        this.getPageData();
                    }else if (this.manageType === 'userPage') {
                        this.getUserPageData();
                    }else{
                        this.getBorrowInfoPageData();
                    }
                }
            },
            changePage(){
                if (this.changePageIndex>0&&this.changePageIndex<=this.totalPage) {
                    this.curpage = this.changePageIndex;
                    this.changePageIndex = null;
                    if (this.manageType === 'bookPage') {
                        this.getPageData();
                    }else if (this.manageType === 'userPage') {
                        this.getUserPageData();
                    }else{
                        this.getBorrowInfoPageData();
                    }
                }else{
                    alert("The number is error!")
                }
            },
            getFile(e){
                this.uploadImg = e.target.files[0];
                // console.log(this.uploadImg);
            },
            upload(){
                if (this.manageType == 'bookPage') {
                    if (this.updateFlag) {
                        let form = new FormData();
                        form.append('id',this.updateId)
                        form.append('file',this.uploadImg);
                        form.append('name',this.uploadBookName);
                        form.append('code',this.uploadBookCode);
                        form.append('location',this.uploadBookLocation);
                        form.append('type',this.uploadBookType);
                        this.$axios.post(
                            '/management/updateBook',
                            form,
                            {headers:{
                                'Content-Type': 'multipart/form-data',
                            }}
                        )
                        .then((res)=>{
                            // console.log(res);
                            this.uploadImg = null;
                            this.uploadBookName = null;
                            this.uploadBookCode = null;
                            this.uploadBookLocation = null;
                            this.uploadBookType = null;
                            alert("更新成功！");
                            this.pageType = 'bookTable';
                            this.getPageData();
                        })
                        .catch((error)=>{
                            console.log(error);
                        })
                    }else{
                        if (this.uploadImg === null||
                            this.uploadBookName === null||
                            this.uploadBookCode === null||
                            this.uploadBookLocation === null||
                            this.uploadBookType === null) {
                            alert("参数有误！")
                        }else{
                            let form = new FormData();
                            form.append('file',this.uploadImg);
                            form.append('name',this.uploadBookName);
                            form.append('code',this.uploadBookCode);
                            form.append('location',this.uploadBookLocation);
                            form.append('type',this.uploadBookType);
                            this.$axios.post(
                                '/management/addBook',
                                form,
                                {headers:{
                                    'Content-Type': 'multipart/form-data',
                                }}
                            )
                            .then((res)=>{
                                // console.log(res);
                                this.uploadImg = null;
                                this.uploadBookName = null;
                                this.uploadBookCode = null;
                                this.uploadBookLocation = null;
                                this.uploadBookType = null;
                                alert("上传成功！");
                                this.pageType = 'bookTable';
                                this.getPageData();
                            })
                            .catch((error)=>{
                                console.log(error);
                            })
                        }
                    }
                }else if (this.manageType == 'userPage') {
                    if (this.updateFlag) {
                        let data = {
                            'id':this.updateId,
                            'user':this.uploadUser,
                            'username':this.uploadUserName,
                            'password':this.uploadPassword,
                            'department':this.uploadDepartment
                        }
                        this.$axios.post(
                            '/management/updateUser',
                            qs.stringify(data),
                            {headers:{
                                'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                                'X-Requested-With':'XMLHttpRequest',
                            }}
                        )
                        .then((res)=>{
                            // console.log(res);
                            this.uploadUser = null;
                            this.uploadPassword = null;
                            this.uploadDepartment = null;
                            this.uploadUserName = null;
                            alert("更新成功！");
                            this.pageType = 'userTable';
                            this.getUserPageData();
                        })
                        .catch((error)=>{
                            console.log(error);
                        })
                    }else{
                        if (this.uploadUser === null||
                            this.uploadPassword === null||
                            this.uploadDepartment === null||
                            this.uploadUserName === null) {
                            alert("参数有误！")
                        }else {
                            let data = {
                                'user':this.uploadUser,
                                'password':this.uploadPassword,
                                'department':this.uploadDepartment,
                                'username':this.uploadUserName
                            }
                            this.$axios.post(
                                '/management/addUser',
                                qs.stringify(data),
                                {headers:{
                                    'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                                    'X-Requested-With':'XMLHttpRequest',
                                }}
                            )
                            .then((res)=>{
                                this.uploadUser = null;
                                this.uploadPassword = null;
                                this.uploadDepartment = null;
                                this.uploadUserName = null;
                                alert("上传成功！");
                                this.pageType = 'userTable';
                                this.getUserPageData();
                            })
                            .catch((error)=>{
                                console.log(error);
                            })
                        }
                    }       
                }
                    
            },
            turn(){
                if (this.manageType == 'bookPage') {
                    this.pageType = 'bookTable';
                    this.getPageData();
                }else if (this.manageType == 'userPage') {
                    this.pageType = 'userTable';
                    this.getUserPageData();
                }
            },
            changeToBookPage(){
                this.manageType = 'bookPage';
                this.pageType = 'bookTable';
                this.curpage = 1;
                this.getPageData();
            },
            changeToUserPage(){
                this.manageType = 'userPage';
                this.pageType = 'userTable';
                this.curpage = 1;
                this.getUserPageData();
            },
            changeToBorrowInfoPage(){
                this.manageType = 'borrowInfoPage';
                this.curpage = 1;
                this.getBorrowInfoPageData();
            },
            getUserPageData(){
                let data = {
                    'currentPage':this.curpage,
                    'pageSize':this.pageSize,
                }
                this.$axios.post(
                    '/management/getUsersInfoByPage',
                    qs.stringify(data),
                    {headers:{
                        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                        'X-Requested-With':'XMLHttpRequest',
                    }}
                )
                .then((res)=>{
                    this.userData = res.data.items;
                    this.totalPage = res.data.totalPage;
                })
                .catch((error)=>{
                    console.log(error);
                })
            },
            addUser(){
                this.pageType = 'uploadUser';
                this.updateFlag = false;
            },
            updateUser(id){
                this.pageType = 'uploadUser';
                this.updateFlag = true;
                this.updateId = id;
            },
            deleteUser(id){
                let data = {'user_id':id};
                this.$axios.post(
                    '/management/deleteUser',
                    qs.stringify(data),
                    {headers:{
                        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                        'X-Requested-With':'XMLHttpRequest',
                    }}
                )
                .then((res)=>{
                    alert("删除成功！");
                    this.getUserPageData();
                })
                .catch((error)=>{
                    console.log(error);
                })
            },
            getBorrowInfoPageData(){
                let data = {
                    'currentPage':this.curpage,
                    'pageSize':this.pageSize,
                }
                this.$axios.post(
                    '/management/getRecordInfoByPage',
                    qs.stringify(data),
                    {headers:{
                        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                        'X-Requested-With':'XMLHttpRequest',
                    }}
                )
                .then((res)=>{
                    this.borrowInfoData = res.data.items;
                    this.totalPage = res.data.totalPage;
                })
                .catch((error)=>{
                    console.log(error);
                })
            },
            deleteBorrowInfo(book_id,user_id){
                let data = {
                    'userId': user_id,
                    'bookId': book_id
                }
                this.$axios.post(
                    '/management/deleteRecord',
                    qs.stringify(data),
                    {headers:{
                        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                        'X-Requested-With':'XMLHttpRequest',
                    }}
                )
                .then((res)=>{
                    alert("删除成功！");
                    this.getBorrowInfoPageData();
                })
                .catch((error)=>{
                    console.log(error);
                })
            }
        }
    }
</script>


