<template>
    <div id="app">
        <div class="main" v-bind:style="{minHeight:(fullHeight-50)+'px'}">
            <router-view></router-view> 
        </div>
        <footer>
            深圳大学&nbsp;&nbsp;|&nbsp;&nbsp;&copy;实验室513&nbsp;&nbsp;|&nbsp;&nbsp;版权所有
        </footer> 
    </div>

</template>

<script>
    export default {
        name: 'App',
        data(){
            return {
                fullHeight:document.documentElement.clientHeight,
            }
        },
        mounted(){
            const that = this;
            window.onsize = () => {
                return (() => {
                    window.fullHeight = document.documentElement.clientHeight;
                    that.fullHeight = window.fullHeight
                })()
            }
        },
        watch:{
            fullHeight(val){
                if(!this.timer){
                this.fullHeight = val;
                this.timer = true;
                let that = this;
                setTimeout(function(){
                    that.timer = false;
                },400)
            }
        }
    }
}
</script>

<style>
    body {
        margin: 0;
    }
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
        min-width: 600px;
        background-image: url(../static/images/bg.gif);
        height: 100%;
    }
/*    .main {
        padding-bottom: 50px;
    }*/
    footer {
        /*position: absolute;*/
        height: 50px;
        line-height: 50px;
        font-size: 1.1rem;
        background-image: url(../static/images/bg.gif);
/*        position:absolute;
        bottom:-50px;
        left:25%;
        width:50%;*/
    }
    ul,li {
        list-style: none;
        margin: 0;
        padding: 0;
    }
    a {
        text-decoration: none;
    }
</style>



