<template>
    <div>
        <div class="login-wrap">
            <h3>借书系统登录</h3>
            <form>
                <input class="username-input" type="text" placeholder="请输入用户名" v-model="username" autocomplete=username>
                <input class="password-input" type="password" placeholder="请输入密码" v-model="password" autocomplete=current-password>
                <p v-show="showTip" class="login-tips">{{tips}}</p>
                <button class="login-submit" @click.prevent="login()">登录</button>
            </form>  
        </div>
    </div>
</template>

<style scroped>
.login-wrap {
  padding-top: 50px;
  text-align:center;
}
.login-wrap > h3 {
  padding: 30px;
  margin: 0;
}
.username-input,
.password-input {
  display:block;
  width:350px; 
  height:40px; 
  line-height:40px; 
  margin:0 auto; 
  margin-bottom: 20px; 
  outline:none; 
  border:1px solid #888; 
  padding:10px; 
  box-sizing:border-box;
}
.login-submit {
  display:block;
  width:350px; 
  height:40px; 
  line-height: 40px; 
  margin:0 auto; 
  border:none; 
  background-color:#41b883; 
  color:#fff; 
  font-size:16px; 
  margin-bottom:5px;
  cursor: pointer;
}
.login-tips{
    color: #f00;
}
</style>

<script>
import qs from 'qs'
export default{
    data(){
        return{
            username: '',
            password: '',
            tips:"",
            showTip:false,
        }
    },
    methods:{
        login(){
            if(this.username == "" || this.password == ""){
                alert("请输入用户名或密码")
            }else{
                let data = {'username':this.username,'password':this.password}
                /*接口请求*/
                this.$axios.post(
                    '/login/loginById',
                    // data,
                    qs.stringify(data),
                    {
                        headers:{
                            'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                            'X-Requested-With':'XMLHttpRequest',
                        }
                    }
                )
                .then((res)=>{
                    // console.log(res);
                    if (res.data.result=="true") {
                        if (res.data.department=="管理员") {
                            this.$router.push({name:'management'});
                        }else{
                            this.$router.push({name:'BookShelf'});
                        }
                    }else{
                        this.tips="账号密码不匹配，请重新登录";
                        this.showTip = true;
                    }
                })
                .catch((error)=>{
                    console.log(error)
                })
            }
        },
    }
}
</script>


