<template>
    <main>
        <span class="CPW-title">修改密码</span>
        <form>
            <div class="passwordForm">
                <span class="passwordTips">旧密码：</span>
                <input type="password" placeholder="请输入旧密码" class="passwordInput" v-model:value="oldPassword" autocomplete=current-password> 
            </div>
            <div class="passwordForm">
                <span class="passwordTips">新密码：</span>
                <input type="password" placeholder="请输入新密码" class="passwordInput" v-model:value="newPassword" autocomplete=current-password>
            </div>
            <div class="passwordForm">
                <span class="passwordTips">重复新密码：</span>
                <input type="password" placeholder="请再次输入新密码" class="passwordInput" v-model:value="re_newPassword" autocomplete=current-password>
            </div>
        </form>
        <span v-if="showTips" class="tips">{{Tips}}</span>
        <button class="return-btn" @click="changed()">返回</button>
        <button class="submit-btn" @click="change()">修改</button>
    </main>
</template>
<style scoped>
    main {
        max-width:1150px;
        margin: 0 auto;
        padding: 0 50px;
    }
    .CPW-title {
        display: block;
        width: 100%;
        border-bottom: 1px solid #bbb;
        font-size: 36px;
        font-weight: 500;
        line-height: 1.1;
        margin: 20px 0 10px 0;
        text-align: left;
        padding-top: 20px;  
        padding-bottom: 10px;     
    }
    .passwordForm {
        padding: 10px;
        line-height: 1.5rem;
    }
    .passwordForm:first-child {
        padding-top: 50px;
    }
    .passwordTips {
        display: inline-block;
        width: 40%;
        text-align: right;
        font-size: 1.5rem;
    }
    .passwordInput {
        display: inline-block;
        width: 20%;
        margin-right: 20%;
        text-align: left;
        padding: 0;
        border: 1px solid #bbb;
        line-height: 1.5rem;
        vertical-align: top;
    }
    .return-btn,
    .submit-btn {
        border: 1px solid #ccc;
        border-radius: 4px;
        padding: 6px 12px;
        background-color: #337ab7;
        cursor: pointer;
        color: #fff;
        outline: none;
    }
    .tips {
        display: block;
        color: #fa007f;
        font-size: 12px;
        padding: 10px 5px;
    }
</style>
<script>
    import qs from 'qs'
    export default{
        data(){
            return {
                oldPassword:'',
                newPassword:'',
                re_newPassword:'',
                showTips:false,
                Tips:'',
            }
        },
        methods:{
            change(){
                // console.log(this.oldPassword);
                if (this.newPassword!=this.re_newPassword) {
                    this.showTips = true;
                    this.Tips = "输入的两次密码不一致";
                    return;
                }else{
                    let data = {
                        'oldPassword' : this.oldPassword,
                        'newPassword' : this.newPassword,
                        'newPassword_re' : this.re_newPassword,
                    }
                    this.$axios.post(
                        '/user/changePassword',
                        qs.stringify(data),
                        {headers:{
                            'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                            'X-Requested-With':'XMLHttpRequest',
                        }}
                    )
                    .then((res)=>{
                        if (res.data.result=="true") {
                            alert(res.data.info);
                            this.$router.push({name:'BookShelf'});
                        }else{
                            this.showTips = true;
                            this.Tips = res.data.info;
                        }
                    })
                    .catch((error)=>{
                        this.$router.push({name:'Login'});
                    })
                }
                
            },
            changed(){
                this.$router.push({name:'BookShelf'});
            }
        }
    }
</script>


