<template>
    <main>
        <span class="BDtitle">书本详情</span>
        <div class="book-info"> 
            <div class="book-img">
                <img v-bind:src="'../../../static/images/'+book_id+'.jpg'">
            </div>
            <div class="book-detail-info">
                <span class="book-item">名称 ：</span>
                <span class="book-item-detail">{{name}}</span>
                <span class="book-item">编号 ：</span>
                <span class="book-item-detail">{{code}}</span>
                <span class="book-item">类型 ：</span>
                <span class="book-item-detail">{{type}}</span>
                <span class="book-item">位置 ：</span>
                <span class="book-item-detail">{{location}}</span>
                <span class="book-item">借阅情况 ：</span>
                <span class="book-item-detail">{{borrower}} {{borrowerInfo}}</span>
            </div>
        </div>
        <span class="brief">简介</span>
        <span class="brief-detail">{{brief}}</span>
        <button class="return-btn" @click="changed()">返回</button>
    </main>
</template>
<style scoped>
    main {
        max-width:1150px;
        margin: 0 auto;
        padding: 0 50px;
    }
    .BDtitle {
        display: block;
        width: 100%;
        border-bottom: 1px solid #bbb;
        font-size: 36px;
        font-weight: 500;
        line-height: 1.1;
        margin: 20px 0 10px 0;
        text-align: left;
        padding-top: 20px;  
        padding-bottom: 10px;     
    }
    .book-info {
        display: flex;
    }
    .book-img {
        width: 35%;
        margin-right: 5%;
    }
    .book-img > img {
        max-width: 300px;
        max-height: 300px;
        width: 100%;
        height: 100%;
    }
    .book-detail-info {
        width: 60%;
        display: flex;
        flex-wrap: wrap;
    }
    .book-item{
        display: block;
        width: 20%;
        font-size: 1.2rem;
        white-space: nowrap;
        overflow: hidden;
    }
    .book-item-detail {
        display: block;
        /*flex-basis: ;*/
        width: 80%;
        text-align: left;
        font-size: 1.2rem;
        white-space: nowrap;
        overflow: hidden;
    }
    .brief {
        display: block;
        font-size: 1.3rem;
        text-align: left;
        padding: 20px;
        color: #ec0b0b;
    }
    .brief-detail {
        display: block;
        text-align: left;
        font-size: 1.1rem;
        text-indent: 2em;
    }
    .return-btn {
        margin-top: 20px;
        height: 35px;
        width: 60px;
        border-radius: 5px;
        background-color: #337ab7;
        border: 1px solid #337ab7;
        cursor: pointer;
        color: #fff;
        outline: none;    
    }
    .return-btn:hover {
        background-color: #2a5885;
    }
</style>
<script>
    import qs from 'qs'
    export default{
        data(){
            return {
                book_id:this.$route.params.book_id,
                brief:"",
                code:"",
                location:"",
                name:"",
                borrower:"",
                borrowerInfo:"",
                type:"",
            }
        },
        created:function(){
                this.getBookDetail();
        },
        methods:{
            getBookDetail(){
                let data = {'book_id':this.book_id}
                this.$axios.post(
                    '/book/getBooksDetail',
                    qs.stringify(data),
                    {headers:{
                        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                        'X-Requested-With':'XMLHttpRequest',
                    }}
                )
                .then((res)=>{
                    this.brief = res.data.brief;
                    this.code = res.data.bookinfo.code;
                    this.location = res.data.bookinfo.location;
                    this.name = res.data.bookinfo.name;
                    if (res.data.borrower==null) {
                        this.borrower = "";
                        this.borrowerInfo = res.data.borrowerInfo;
                    }else{
                        this.borrower = res.data.borrower;
                        this.borrowerInfo = res.data.borrowerInfo;
                    }
                    switch(parseInt(res.data.bookinfo.type)){
                        case 1:
                            this.type = "商业相关" 
                            break;
                        case 2:
                            this.type = "安卓相关"
                            break;
                        case 3:
                            this.type = "IOS相关"
                            break;
                        case 4:
                            this.type = "后台相关"
                            break;
                        case 5:
                            this.type = "前端相关"
                            break;
                        case 6:
                            this.type = "设计相关"
                            break;
                        case 7:
                            this.type = "其它"
                            break;
                    }
                })
                .catch((res)=>{
                    this.$router.push({name:'Login'});
                })
            },
            changed(){
                this.$router.push({name:'BookShelf'});
            },
        }
    }
</script>


