<template>
    <main>
        <div class="main-header">
            <span>书架</span>
            <div class="classification">
                <button class="select-but" @click="active=!active"><span></span></button>
                <div v-show="active" class="select-content">
                    <ul >
                        <li v-for="(item,index) in allType" @click="changeType(index,item)" :class="clickIndex == index ? 'selectd' : ''" >{{item.type}}</li>
                    </ul>
                </div>
                <span class="book-type">{{selectdType.type}}</span>
            </div> 
        </div>
        <div class="bookshelf">
            <div class="book-search">
                <input type="text" class="book-search-input" v-model:value="bookName" @keyup.enter="searchBook()">
                <input type="button" class="book-search-but" value="搜索" @click="searchBook()">
            </div>
            <div class="bookshelf-content">
                <ul class="bookshelf-contentUl">
                    <li v-for="dataItem in bookshelfdata" class="bookshelfItem">
                        <div>
                            <div class="bookImg"><img v-bind:src="'../../../static/images/'+dataItem.id+'.jpg'"></div>
                            <h3 class="book-name">{{dataItem.code}} 《{{dataItem.name}}》</h3>
                            <span v-if="dataItem.record" class="borrower-info">{{dataItem.record.user.username}} 已借出</span>
                            <div class="operate">
                                <button v-bind:disabled="!(dataItem.record==undefined)" :class="{'ban':!(dataItem.record==undefined)}" class="borrower-btn" @click="borrowBook(dataItem.id)">借阅</button>
                                <button class="collect-btn" @click="collectBook(dataItem.id)">收藏</button>
                                <button class="details-btn" @click="showBookDetail(dataItem.id)">详情</button>
                            </div>
                        </div>
                    </li>
                    <li v-for="dataItem in pagesize-bookshelfdata.length" class="bookshelfItem">
                        <div>
                            <div class="bookImg"><img v-bind:src="'../../../static/images/default.jpg'"></div>
                            <h3 class="book-name">暂无</h3>
                            <span v-if="dataItem.record" class="borrower-info">{{dataItem.record}} 已借出</span>
                            <div class="operate">
                                <button class="borrower-btn">借阅</button>
                                <button class="collect-btn">收藏</button>
                                <button class="details-btn">详情</button>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
            <div class="bookshelf-footer">
                <div class="data-info">
                    共有<span>{{total}}</span>条记录，共<span>{{pagecount}}</span>页，当前是第<span>{{curpage}}</span>页
                </div>
                <div class="page-right">
                    <ul class="boot-page">
                        <li>
                            <a href="javascript:void(0)" aria-label="Previous" @click="onFirstClick()">
                                <span aria-hidden="true">&laquo;</span>
                            </a>
                        </li>
                        <li>
                            <a href="javascript:void(0)" aria-label="Next" @click="onPrevClick()">
                                <span aria-hidden="true">‹</span>
                            </a>
                        </li>
                        <li v-for="page in pages" :class="curpage === page ? 'active' : ''">
                            <a href="javascript:void(0)" v-text="page" @click="onPageClick(page)"></a>
                        </li>
                        <li>
                            <a href="javascript:void(0)" aria-label="Next" @click="onNextClick()">
                                <span aria-hidden="true">›</span>
                            </a>
                        </li>
                        <li>
                            <a href="javascript:void(0)" aria-label="Next" @click="onLastClick()">
                                <span aria-hidden="true">&raquo;</span>
                            </a>
                        </li>
                    </ul>
                    <div class="setActive">
                        <input v-model="changeCurpages" @keyup.enter="changeTable"><span> / {{pagecount}} 页</span>
                    </div>
                    <button class="setButton" @click="changeTable">确定</button>
                </div>
            </div>
        </div>
    </main>
</template>

<style scoped>
    main {
        max-width:1150px;
        margin: 0 auto;
        padding: 0 50px;
    }
    .main-header {
        margin: 40px auto 20px;
        border-bottom: 1px solid #ccc;
        display: flex;
        justify-content: space-between;
    }
    .main-header >span{
        font-size: 36px;
        font-weight: 500;
        line-height: 1.1;
        margin: 20px 0 10px 0;
    }
    .classification {
        height: 34px;
        margin: 20px 0 10px 0;
    }
    .select-but {
        display: inline-block;
        width: 34px;
        height: 34px;
        padding: 6px 12px;
        border: 1px solid #ccc;
        outline: 0;
        background-color: #fff;
        border: 1px solid #ccc;
        border-radius: 4px;
        cursor: pointer;
    }
    .select-but:hover {
        color: #333;
        background-color: #e6e6e6;
        border-color: #adadad;
        box-shadow: inset 0 3px 5px rgba(0, 0, 0, .125);
    }
    .select-but > span {
        display: block;
        border-top: 5px solid #333;
        border-right: 5px solid transparent;
        border-left: 5px solid transparent;
        border-bottom: 5px solid transparent;
        margin: 3px 0 0 0;
    }
    .book-type {
        display: inline-block;
        width: 64px;
        height: 20px;
        margin-left: -2px; 
        padding: 6px 12px;
        border: 1px solid #ccc;
        vertical-align: top;
        border-radius: 4px;
    }
    .select-content {
        position: absolute;
        width: 124px;
        border: 1px solid #ccc;
        border-radius: 4px;
        background-color: #fff;
        z-index: 999;
    }
    .select-content > ul {
        padding: 10px 0;
    }
    .select-content > ul > li{
        line-height: 1.7;
        cursor: pointer;
    }
    .select-content > ul > li:hover {
        background-color: #f4f4f4;
    }
    .selectd {
        background-color: #3572ad;
    }
    .bookshelf {
        border: 1px solid #ccc;
        border-radius: 4px;
        box-shadow: 1px 1px 1px #888888;
    }
    .book-search {
        padding: 10px;
        display: flex;
    }
    .book-search-input {
        width: 90%;
        height: 28px;
        padding: 3px;
        border: 1px solid #ccc;
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
    }
    .book-search-but {
        width: 10%;
        /*margin-left: -10%; */
        border: 1px solid #ccc;
        border-left: 0; 
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
        background-color: #fff;
        cursor: pointer;
    }
    .bookshelf-content {
        border-top:1px solid #ccc;
        border-bottom: 1px solid #ccc;
        padding: 10px;
    }
    .bookshelf-contentUl {
        display: flex;
        justify-content: space-between;      
        flex-wrap: wrap; 
    }
    .bookshelfItem {
        max-width: 20%;
        min-width: 200px;
        flex: 0 0 22%;
        padding: 10px;
        background-color: #fff;
        margin: 10px 0;
        border-radius:10px;
        box-shadow: 1px 1px 1px #888888;
    }
    .bookImg {
        height: 150px;
        width: 150px;
        margin: 0 auto;
    }
    .bookImg > img {
        height: 150px;
        width: 150px;
    }
    .book-name {
        overflow: hidden;
        white-space: nowrap;
        text-overflow:ellipsis;
        margin-bottom: 18px;
    }
    .borrower-info {
        /*padding-bottom: 18px;*/
        color: #c20020;
        line-height: 21px;
        font-size: .8rem;
        text-align: left;
        display: block;
        margin-top: -21px;
    }
    .operate {
        display: flex;
        justify-content: space-between;
    }
    .borrower-btn,
    .collect-btn,
    .details-btn {
        border: 1px solid #ccc;
        border-radius: 4px;
        padding: 6px 12px;
        background-color: #337ab7;
        cursor: pointer;
        color: #fff;
        outline: none;
    }
    .collect-btn {
        background-color: #fff;
        color: #aaa;
    }
    .details-btn {
        background-color: #5abad8;
    }
    .borrower-btn:hover {
        background-color: #2a5885;
    }
    .ban {
        cursor: not-allowed;
    }
    .bookshelf-footer {
        height: 42px;
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .page-right {
        display: flex;
        justify-content: flex-end;
    }
    .boot-page {
        display: flex;
    }
    .boot-page > li:first-child > a {
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
        border-left: 1px solid #bbb;
    }
    .boot-page > li:last-child > a {
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
    }
    .boot-page > li > a {
        display: inline-block;
        padding: 6px 12px;
        height: 28px;
        width: 16px;
        border: 1px solid #bbb;
        border-left: 0;
        color: #337ab7;
        line-height: 28px; 
    }
    .boot-page > li > a:hover {
        text-decoration: none;
    }
    .boot-page > li:hover {
        background-color: #ECECEC;
    }
    .boot-page > .active > a {
        background-color: #337ab7;
        border-color: #337ab7;
    }
    .boot-page > .active:hover {
        background-color: #337ab7;
    }
    .boot-page > .active > a {
        display: inline-block;
        color: #FFF;
    }
    .data-info {
        line-height: 42px;
        padding-right: 10px;
        text-indent: 1em;
        float:left;
    }
    .data-info > span {
        color: #fe7297;
        padding: 0 4px;
    }
    .setActive {
        height: 42px;
        line-height: 42px;
        padding: 0 0 0 10px;
    }
    .setActive > input {
        width: 25px;
        text-indent: .3em;
        margin-right: 3px;
        border: 1px solid #bbb;
        border-radius: 3px;
    }
    .setButton {
        cursor: pointer;
        border-radius: 5px;
        background-color: #eee;
        margin-left: 10px;
        margin: 8px 10px;
        height: 25px;
        border: 1px solid #bbb;
    }
    .setButton:hover {
        background-color: #ECECEC;
    }
</style>
<script>
    import qs from 'qs'
    export default {
        data(){
            return {
                active:false,
                selectdType: {"type":"所有书籍","id":0},
                //书的类别
                allType:[
                    {
                        "type":"所有书籍",
                        "id":0,
                    },
                    {
                        "type":"商业相关",
                        "id":1,
                    },
                    {
                        "type":"安卓相关",
                        "id":2,
                    },
                    {
                        "type":"IOS相关",
                        "id":3,
                    },
                    {
                        "type":"后台相关",
                        "id":4,
                    },
                    {
                        "type":"前端相关",
                        "id":5,
                    },
                    {
                        "type":"设计相关",
                        "id":6,
                    },
                    {
                        "type":"其他",
                        "id":7
                    } 
                ],
                //选定的类别号
                clickIndex: 0,
                //展示的书数据
                bookshelfdata:[],
                //当前分页
                curpage: 1,
                //每页数量
                pagesize:8,
                //图片位置
                ImgAdd:[],
                //总书量
                total:0,
                //总页数
                pagecount:0,
                //表格尾展示的索引页数
                pageFooterCount:5,
                //表格尾展示的索引页
                pages:[],
                changeCurpages:'',
                bookName:'',
                searchFlag:false,
            }
        },
        created:function(){
            this.getPages();
        },
        methods:{
            changeType(index,type){
                this.active = !this.active;
                this.selectdType = type;
                this.clickIndex = index;
                this.curpage = 1;
                this.searchFlag = false;
                this.getPages();     
            },
            getChangedPages(){
                let data = {
                        'currentPage':this.curpage,
                        'pageSize':this.pagesize,
                        'type':this.selectdType.id
                    }
                this.$axios.post(
                    '/book/getBooksByPage',
                    qs.stringify(data),
                    {headers:{
                        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                        'X-Requested-With':'XMLHttpRequest',
                    }}
                )
                .then((res)=>{
                    this.bookshelfdata = res.data.items;
                    this.pagecount = res.data.totalPage;
                    this.getFooter();
                })
                .catch((error)=>{
                    this.$router.push({name:'Login'});
                })
            },
            getPages(){
                if (this.searchFlag) {
                    this.getSearchPages();
                }else{
                    if (this.selectdType.id==0) {
                        let data = {
                            'currentPage':this.curpage,
                            'pageSize':this.pagesize
                        }
                        /*接口请求*/
                        this.$axios.post(
                            '/book/getBooksByPage',
                            qs.stringify(data),
                            {headers:{
                                'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                                'X-Requested-With':'XMLHttpRequest',
                            }}
                        )
                        .then((res)=>{
                            this.bookshelfdata = res.data.items;
                            this.curpage = parseInt(res.data.currentPage);
                            this.total = res.data.totalRows;
                            this.pagecount = res.data.totalPage;
                            this.getFooter();
                        })
                        .catch((error)=>{
                            this.$router.push({name:'Login'});
                        })
                    }else {
                        this.getChangedPages();
                    }
                }
            },
            searchBook(){
                this.curpage = 1;
                this.searchFlag = true;
                this.getPages();
            },
            getSearchPages(){
                let data = {
                    'keyWord' : this.bookName,
                    'currentPage' : this.curpage,
                    'pageSize' : this.pagesize
                }
                this.$axios.post(
                    '/book/searchBook',
                    qs.stringify(data),
                    {headers:{
                        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                        'X-Requested-With':'XMLHttpRequest',
                    }}
                )
                .then((res)=>{
                    this.bookshelfdata = res.data.items;
                    this.pagecount = res.data.totalPage;
                    this.getFooter();
                })
                .catch((error)=>{
                    this.$router.push({name:'Login'});
                })
            },
            borrowBook(book_id){
                let data = {'book_id':book_id,}
                this.$axios.post(
                    '/record/borrowBook',
                    qs.stringify(data),
                    {headers:{
                        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                        'X-Requested-With':'XMLHttpRequest',
                    }}
                )
                .then((res)=>{
                    this.getPages();
                    this.$emit("flesh");
                })
                .catch((error)=>{
                    this.$router.push({name:'Login'});
                })
            },
            collectBook(book_id){
                let data = {'book_id':book_id}
                this.$axios.post(
                    '/collect/collectBook',
                    qs.stringify(data),
                    {headers:{
                        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',
                        'X-Requested-With':'XMLHttpRequest',
                    }}
                )
                .then((res)=>{
                    if (res.data.result=="true") {
                        alert("已添加到我的收藏");
                        this.$emit("flesh");
                    }else{
                        alert(res.data.info);
                    }
                    
                })
                .catch((error)=>{
                    // this.$router.push({name:'Login'});
                })
            },
            showBookDetail(book_id){
                this.$router.push({name:'BookDetail',params:{book_id:book_id}});
            },
            onFirstClick(){
                this.curpage = 1;
                this.getPages();
            },
            onPrevClick(){
                if (this.curpage-1>0) {
                    this.curpage -= 1;
                }
                this.getPages();
            },
            onPageClick(page){
                this.curpage = page;
                this.getPages();
            },
            onNextClick(){
                if (this.curpage+1<=this.pagecount) {
                    this.curpage += 1;
                }
                this.getPages();
            },
            onLastClick(){
                this.curpage = this.pagecount;
                this.getPages();
            },
            changeTable(){
                this.curpage = parseInt(this.changeCurpages);
                this.getPages();
                this.changeCurpages = "";
            },
            getFooter(){
                this.pages = [];
                if (this.pagecount<this.pageFooterCount) {
                    for (var i =  1; i <= this.pagecount; i++) {
                        this.pages.push(i);    
                    }
                }else {
                    if (this.curpage-Math.floor(this.pageFooterCount/2)<=0) {
                        for (var i =  1; i <= this.pageFooterCount; i++) {
                            this.pages.push(i);    
                        }
                    }else if(this.curpage+Math.floor(this.pageFooterCount/2)>this.pagecount){
                        for (var i = this.pagecount-this.pageFooterCount; i <= this.pagecount; i++) {
                            this.pages.push(i); 
                        }
                    }else {
                        for (var i = this.curpage-Math.floor(this.pageFooterCount/2); i <= this.curpage+Math.floor(this.pageFooterCount/2); i++) {
                            this.pages.push(i);    
                        }
                    }
                }
            },
        }
    }
</script>


